@import url(https://fonts.googleapis.com/css?family=Orbitron|Roboto|Text+Me+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100vw;
    color: #005555;
    padding: 20px 0;
}

.header h1 {
    text-align: center;
    font-size: 25px;
}

@media screen and (max-width:700px) {
    .header {
        padding: 0;
    }
}
.quote-container {
  flex: 1 1;
  height: 435px;
  box-shadow: 0 0 1px 1px rgb(160, 227, 219);
  border-radius: 15px;
  text-align: center;
  margin-left: 10px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  font-family: "Text Me One", sans-serif;
}

.quote-container p {
  margin: 0;
}

.quote-text {
  padding: 0 20px;
  font-size: 23px;
  color: #2c3d55;
}

.quotation-mark {
  color: #005555;
  font-size: 50px;
  font-family: serif, sans-serif, monospace;
}

.left {
  padding: 0 0 0 30px;
  text-align: left;
}

.right {
  padding: 0 30px 0 0;
  text-align: right;
}

.quote-author {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  color: #005555;
}

.quote-author:before {
  content: "-- ";
}

@media screen and (max-width: 700px) {
  .quote-container {
    height: auto;
    margin: 0 5px;
    padding: 10px;
  }
  .quote-text {
    padding: 0;
  }
  .quote-author {
    padding: 10px 0;
  }
  .quotation-mark {
    font-size: 35px;
  }
  .quotation-mark {
    display: none;
  }
}

.btn {
    padding: 10px 0;
    min-width: 90px;
    margin: 10px;
    outline: none;
    border-radius: 8px;
    border: 1px solid transparent;
    cursor: pointer;
    -webkit-transition: all 2ms ease;
    transition: all 2ms ease;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    background-color: #e5e5e5;
}

.btn-start {
    box-shadow: 0 0 1px 1px #6dc9dd;
}

.btn-start:hover {
    box-shadow: 0 0 2px 2px #6dc9dd;
}

.btn-start:active {
    background: -webkit-linear-gradient(#f4f3ea, #6dc9dd);
    background: linear-gradient(#f4f3ea, #6dc9dd);
}

.btn-stop {
    box-shadow: 0 0 1px 1px #d36582;
}

.btn-stop:hover {
    box-shadow: 0 0 2px 2px #d35682;
}

.btn-stop:active {
    background: -webkit-linear-gradient(#f4f3ea, #d36582);
    background: linear-gradient(#f4f3ea, #d36582);
}

.btn-reset {
    box-shadow: 0 0 1px 1px #b8cdf8;
}

.btn-reset:hover {
    box-shadow: 0 0 2px 2px #b8cdf8;
}

.btn-reset:active {
    background: -webkit-linear-gradient(#f4f3ea, #becdf8);
    background: linear-gradient(#f4f3ea, #becdf8);
}
.settings {
  padding: 20px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
}

.settings h2 {
  margin-left: 15px;
  font-size: 18px;
  width: 100%;
  color: #005555;
}

.settings label {
  display: inline-block;
  text-align: center;
  font-size: 18px;
}

.settings input[type="number"] {
  border: 0;
  font-size: 18px;
  text-align: center;
  -moz-appearance: textfield;
}

.settings input[type="number"]::-webkit-inner-spin-button,
.settings input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.input-control {
  flex: 1 1 40%;
  text-align: center;
  padding: 19px;
}

.input-group {
  margin-top: 5px;
  box-shadow: 0 0 1px 1px #005555;
  padding: 6px;
  background: #fff;
  border-radius: 6px;
}

.input-control input[type="button"] {
  padding: 5px 12px;
  font-size: 18px;
}

.input-control input[type="button"]:focus {
  box-shadow: 0 0 6px 2px #52cee4;
}

.settings input[type="number"] {
  outline: 0;
  margin: 0 5px;
}

.settings .input-group:focus-within {
  box-shadow: 0 0 6px 2px #277cf1;
  outline: 0;
}

.settings input[type="checkbox"] {
  display: none;
}

.settings .custom-checkbox {
  width: 25px;
  height: 22px;
  margin-right: 15px;
  box-shadow: 0 0 1px 1px #2c3d55;
  border-radius: 3px;
  cursor: pointer;
  position: relative;
  background-color: #ffffff;
  display: inline-block;
  vertical-align: bottom;
}

.settings .custom-checkbox:hover {
  box-shadow: 0 0 2px 2px #52cee4;
}

.settings input[type="checkbox"]:checked + .custom-checkbox {
  background: #2c3d55;
}

.settings input[type="checkbox"]:checked:focus-within {
  border: 3px solid red;
}

.settings input[type="checkbox"]:checked + .custom-checkbox::before {
  content: "";
  position: absolute;
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  width: 15px;
  height: 5px;
  top: 6px;
  left: 4px;
  -webkit-transform: rotate(80deg);
  transform: rotate(315deg);
}

.alaram-sound,
.auto-start {
  padding-top: 20px;
}

@media screen and (max-width: 700px) {
  .settings {
    padding: 0;
    align-items: flex-start;
  }
  .input-control {
    text-align: left;
    padding: 12px;
  }
  .settings h2 {
    margin: 8px;
    text-align: left;
  }
}

@font-face {
    font-family: "digital";
    src: url(/pomodoro-timer/static/media/digital-webfont.5b0f802e.woff) format("woff2"), url(/pomodoro-timer/static/media/digital-webfont.1edd89f1.woff2) format("woff");
    font-weight: normal;
    font-style: normal;
}

.timer-container {
    flex: 1.4 1;
    border-radius: 15px;
    box-shadow: 0 0 1px 2px rgba(102, 51, 153, 0.4);
    padding: 40px 0 0 0;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.timer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.clock {
    height: 200px;
    width: 200px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: "digital", Arial, Helvetica, sans-serif;
    letter-spacing: 5px;
    font-size: 45px;
    font-weight: bold;
    margin: 15px;
}

.session {
    box-shadow: 0 0 5px 10px hsl(180, 100%, 17%), 0 0 10px 5px hsla(180, 100%, 17%, 0.7);
    border: 4px solid hsla(180, 100%, 17%, 0.6);
}

.break {
    box-shadow: 0 0 5px 10px hsl(217, 60%, 47%), 0 0 10px 5px hsla(217, 60%, 47%, 0.7);
    border: 4px solid hsl(217, 60%, 47%, 0.6);
}

@media screen and (max-width: 700px) {
    .clock {
        flex-flow: column wrap;
    }
    .timer-container {
        box-shadow: none;
        margin: 0;
    }
    .timer {
        flex-flow: column wrap;
    }
}
.notifications {
    position: fixed;
    top: 5px;
    right: 5px;
}

.show-msg {
    -webkit-animation: show-msg 4s ease;
            animation: show-msg 4s ease;
    margin: 5px 10px;
    padding: 8px;
    width: 260px;
    text-align: center;
    background-color: rgb(102, 51, 153);
    color: #ffffff;
    border-radius: 5px;
    opacity: 0;
    font-size: 18px;
}

@-webkit-keyframes show-msg {
    0% {
        opacity: 1;
    }
    20% {
        opacity: 0.8;
    }
    40% {
        opacity: 0.6;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 0.2;
    }
    100% {
        opacity: 0;
    }
}

@keyframes show-msg {
    0% {
        opacity: 1;
    }
    20% {
        opacity: 0.8;
    }
    40% {
        opacity: 0.6;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 0.2;
    }
    100% {
        opacity: 0;
    }
}
.todo-container {
  align-self: flex-start;
  min-height: 425px;
  margin-right: 10px;
  flex: 1 1;
  box-shadow: 0 0 1px 1px rgb(255, 255, 0);
  border-radius: 15px;
}

.todo-title {
  text-align: center;
  color: #005555;
  font-size: 25px;
}

.todo-form {
  border: 1px solid #005555;
  display: flex;
  margin: 0 auto;
  border-radius: 15px;
  width: 80%;
}

.todo-input {
  padding: 10px;
  outline: none;
  border: 0;
  color: #2c3d55;
  font-weight: bold;
  font-size: 16px;
  flex: 2 1 auto;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  background: #ffffff;
}

.btn-add-todo {
  outline: none;
  border: 0;
  border-top-right-radius: 14px;
  border-bottom-right-radius: 14px;
  width: 60px;
  background-color: #005555;
  color: #ffffff;
  cursor: pointer;
}

.btn-add-todo:active {
  background: -webkit-linear-gradient(#ffffff, #3f6e9e);
  background: linear-gradient(#ffffff, #3f6e9e);
}

.todo-filter {
  text-align: center;
}

.todo-filter input[type="button"] {
  border: 0;
  outline: 0;
  margin: 3px;
  padding: 3px 10px;
  font-size: 15px;
  cursor: pointer;
}

.todo-items li {
  list-style-type: none;
  padding: 9px;
  font-size: 16px;
  margin: 5px;
}

.todo-items li:hover {
  box-shadow: 0 0 1px 3px #f4f3ea;
  border-radius: 5px;
  background-color: #eaebf4;
  padding: 10px 12px;
}

.completed-task {
  text-decoration: line-through;
  -webkit-text-decoration-color: "#005555";
          text-decoration-color: "#005555";
}

.todo-items input[type="checkbox"] {
  display: none;
}

.todo-items span {
  box-shadow: 0 0 1px 1px #005555;
  border-radius: 3px;
  position: relative;
  margin-right: 10px;
  height: 16px;
  width: 16px;
  display: inline-block;
  vertical-align: middle;
}

.todo-items input[type="checkbox"]:checked + span {
  background-color: #005555;
}

.todo-items input[type="checkbox"]:checked + span::before {
  content: "";
  position: absolute;
  border-left: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  width: 10px;
  height: 3px;
  top: 4px;
  left: 2px;
  -webkit-transform: rotate(80deg);
  transform: rotate(315deg);
}

.remove-btn {
  float: right;
  opacity: 0;
}

.todo-items li:hover .remove-btn {
  opacity: 1;
  color: #dc143c;
}

.no-tasks {
  text-align: center;
  padding: 50px 0;
}

@media screen and (max-width: 700px) {
  .todo-container {
    box-shadow: none;
    align-self: unset;
    margin: 0;
  }
  .todo-form {
    width: 90vw;
  }
  .todo-items {
    padding-left: 10px;
    margin: 0 auto;
  }
}

body {
    padding: 0;
    background: #f4f3ea;
    color: #41463d;
    font-family: "Roboto", sans-serif;
}

.container {
    display: flex;
    flex-flow: row wrap;
}

@media screen and (max-width: 700px) {
    body {
        background-color: #ffffff;
    }
    .container {
        flex-flow: column;
    }
}
