.notifications {
    position: fixed;
    top: 5px;
    right: 5px;
}

.show-msg {
    animation: show-msg 4s ease;
    margin: 5px 10px;
    padding: 8px;
    width: 260px;
    text-align: center;
    background-color: rgb(102, 51, 153);
    color: #ffffff;
    border-radius: 5px;
    opacity: 0;
    font-size: 18px;
}

@keyframes show-msg {
    0% {
        opacity: 1;
    }
    20% {
        opacity: 0.8;
    }
    40% {
        opacity: 0.6;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 0.2;
    }
    100% {
        opacity: 0;
    }
}